<template>
    <div class="about">
        <h1>This is an about page</h1>
        <van-button type="primary" @click="loadData">加载测试数据</van-button>
        <div>
            <div v-for="(resource,i) in resources" :key="i">
                {{resource.fields.name}}
            </div>
        </div>
        <van-button type="primary" @click="addUser">添加数据</van-button>
    </div>
</template>

<script>
    import {Button} from 'vant';

    export default {
        data() {
            return {
                resources: []
            }
        },
        components: {
            [Button.name]: Button
        },
        mounted() {
            this.loadData();
        },
        methods: {
            loadData() {
                let _this = this;
                _this.resources = [];
                this.$axios.get("/user").then(response => {
                    _this.resources = response.data.records || [];
                });

            },
            addUser() {
                let _this = this;

                let data = {
                    "records": [
                        {
                            "fields": {
                                "account": "zhangsan",
                                "password": "111111",
                                "status": "Y",
                                "name": "张三"
                            }
                        }
                    ]
                };
                this.$axios.post("/user", data).then(() => {
                    _this.loadData();
                });
            }
        }
    }
</script>
